import dayjs from "dayjs";
import moment from "moment";

export const addDays = (date, quincena, tareo) => {
  const WEEKEND = [0];
  let daysAdded = 0,
    momentDate = dayjs(date);
  if (tareo === "Mes cerrado") {
    const end = momentDate.clone().add(quincena, "month").subtract(1, "day");
    momentDate = end.clone();
  }

  let endDate = momentDate.clone().add(quincena * 15 - 1, "days"); // fecha final de la primera quincena
  if (tareo === "Lunes a sabado") {
    while (daysAdded < quincena * 14) {
      momentDate = momentDate.add(1, "day");
      if (!WEEKEND.includes(momentDate.weekday())) {
        daysAdded++;
      }
      if (daysAdded === 15 && quincena > 1) {
        // si ha pasado una quincena y no es la primera, actualice la fecha final de la quincena anterior
        endDate = momentDate.clone().subtract(1, "day");
        // calcule la fecha de inicio de la siguiente quincena
        momentDate = endDate.clone().add(1, "day");
        // reinicie la cuenta de días agregados para la nueva quincena
        daysAdded = 0;
        // disminuya el número de quincena actual
        quincena--;
      }
    }
  }
  if (momentDate.day() === 0) {
    momentDate = momentDate.add(1, "day");
  }

  let fecha = momentDate.isValid() ? momentDate.format("YYYY-MM-DD") : null;
  return fecha;
};
